import * as React from 'react';
import type { LinkPluginConfig } from './types';
import LinkButton from './components/next/linkButton';
import { ItemKey, ItemSlot, RenderToolbar, SlotType } from '@ali/4ever-component';

export default function createRenderToolbar(
  locale: LinkPluginConfig['locale']
): RenderToolbar {

  return (props, controller) => {

    const { buttonName, extraProps } = props;

    if (buttonName === ItemKey.link) {
      return (
        <ItemSlot display={SlotType.iconButton}>
          <LinkButton
            controller={controller}
            locale={locale}
            {...extraProps}
          />
        </ItemSlot>
      )
    }

    return null
  };
}
